<template>
	<section class="sub flex" :class="{ push: this.$store.getters.getSectionPush }">
		<span class="wrap">
			<div class="inner">
				<p v-for="day in holidays" :key="`holidays${day.idx}`">
					<el-time-picker
						is-range
						v-model="day.item"
						range-separator="~"
						start-placeholder="Start time"
						end-placeholder="End time"
						format="HH:mm"
						step="00:10"
					>
					</el-time-picker>
				</p>
				<button type="button" @click="addHoliday">add</button>
				<p v-for="(day, idx) in workdays" :key="`workdays${idx}`">
					<el-time-picker
						is-range
						v-model="day.item"
						range-separator="~"
						start-placeholder="Start time"
						end-placeholder="End time"
						format="HH:mm"
						step="00:10"
					>
					</el-time-picker>
				</p>
				<button type="button" @click="confirm">확인</button>
			</div>
		</span>
	</section>
</template>

<script>
export default {
	data() {
		return {
			holidays: [{ idx: 0, item: [new Date().setHours(0, 0, 0), new Date().setHours(23, 0, 0)] }],
			workdays: [{ idx: 0, item: [new Date().setHours(0, 0, 0), new Date().setHours(23, 0, 0)] }],
		};
	},
	methods: {
		addHoliday() {
			this.holidays.push({
				idx: this.holidays.length,
				item: [new Date().setHours(0, 0, 0), new Date().setHours(23, 0, 0)],
			});
		},
		changeItem(day) {
			console.log(day);
		},
		confirm() {
			let cnt = 1;
			for (let holiday of this.holidays) {
				let flag = true;
				for (let workdays of this.workdays) {
					console.log(workdays.item[0]);
					console.log(holiday.item[0]);
					console.log(holiday.item[1]);
					console.log(workdays.item[1]);
					if (workdays.item[0] <= holiday.item[0] && holiday.item[1] <= workdays.item[1]) {
						workdays.push({ holiday: holiday.item });
						flag = false;
					}
				}
				if (flag) {
					this.sAlert(cnt + '번째 휴식 시간에 맞는 근무시간이 없습니다.');
					return;
				}
				cnt++;
			}
		},
	},
};
</script>

